.tracker {
    display: flex;
    position: absolute;
}

.container {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
}

.container_left {
    width: 200px;
    height: 200px;
    transform: translate(-120%, -70%);
    border: 3px solid #333;
}

.container_right {
    width: 200px;
    height: 200px;
    transform: translate(-35%, -50%);
    border: 3px solid #333;
}

.tracker {
    width: 10px;
    height: 10px;
    left: 5px;
    top: 5px;
    background-color: black;
    transform: translate(calc(-50% + 185px * var(--x)), calc(-50% + 185px * var(--y)));
}

.tracker_eye {
    position: absolute;
    width: 10px;
    height: 10px;
    left: 30px;
    top: 55px;
    background-color: black;
    transform: translate(calc(-50% + 150px * var(--x)), calc(-50% + 120px * var(--y)));
}